import React from "react";
import { Grid, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { useSpring, animated } from "react-spring";

const styles = makeStyles((theme) => ({
  root: { marginTop: 120 },
  column: { height: 500 },
}));
const About = () => {
  const classes = styles();
  const props = useSpring({
    from: { opacity: 0 },
    to: { opacity: 1 },
    config: { delay: 1000, duration: 5000 },
  });

  return (
    <div className={classes.root}>
      <Grid container spacing={2}>
        <Grid
          item
          container
          xs={12}
          md={8}
          className={classes.column}
          justify="center"
          spacing={2}
          wrap="nowrap"
        >
          <Grid item xs={4}>
            <animated.div style={props}>
              <Typography>
                item item item item item itemitem item itemitem item itemitem
                item itemitem item itemitem item itemitem item itemitem item
                itemitem item item item item itemitem item itemitem item item
                item item itemitem item item item item itemy
              </Typography>
            </animated.div>
          </Grid>
          <Grid item xs={4}>
            item
          </Grid>
          <Grid item xs={4}>
            item
          </Grid>
        </Grid>
        <Grid item xs={12} md={4} className={classes.column}>
          contatc us
        </Grid>
      </Grid>
    </div>
  );
};

export default About;
