import React from "react";
import {} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Slider from "react-slick";

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import image1 from "../../../media/image/Boat.jpg";
import image2 from "../../../media/image/penguins.jpg";
import image3 from "../../../media/image/DT-CAROUSEL-1.jpg";
import image4 from "../../../media/image/DT-CAROUSEL-2.jpg";

import { useSpring, animated } from "react-spring";

const styles = makeStyles((theme) => ({
  root: { width: "100%" },
  image: { height: 600 },
}));

const HomeCarousel = () => {
  const classes = styles();
  const carouselData = [
    {
      id: 1,
      title: "title 1",
      image: image2,
      description: "description of the image 1",
    },
    {
      id: 2,
      title: "title 2",
      image: image1,
      description: "description of the image 2",
    },
    {
      id: 3,
      title: "title 3",
      image: image3,
      description: "description of the image 1",
    },
    {
      id: 4,
      title: "title 4",
      image: image4,
      description: "description of the image 4",
    },
  ];

  const springAnimation = useSpring({
    from: { marginTop: -600 },
    to: { marginTop: 0 },
    config: { duration: 5000 },
  });
  var settings = {
    dots: true,
    infinite: true,
    speed: 650,
    slidesToShow: 1,
    slidesToScroll: 1,
    arrows: false,
    autoplaySpeed: 6000,
    autoplay: true,
    centerPadding: "60px",
  };
  return (
    <animated.div style={springAnimation} className={classes.root}>
      <Slider {...settings}>
        {carouselData.map((item) => {
          return (
            <div
              key={item.id}
              style={{
                height: "inherit",
                width: "100%",
              }}
            >
              <img
                src={item.image}
                alt={item.title}
                style={{
                  height: "inherit",
                  width: "100%",
                  backgroundSize: "contain",
                }}
              />
            </div>
          );
        })}
      </Slider>
    </animated.div>
  );
};

export default HomeCarousel;
